@import url(https://fonts.googleapis.com/css2?family=Peddana&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
/* 	font: inherit; */
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.header {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 4px;
	height: 3vw;
	margin-bottom: 1vw;
	font-size: 2vw;
	background-color: #d9f7c7;
}

.headerLink {
	position: relative;
	cursor: pointer;
	margin: 0 1.2vw;
	transition: all 5s;
}

.headerLink:before {
	content: "";
	position: absolute;
	margin-left: auto;
	margin-right: auto;
	left: 0;
	right: 0;
	bottom: 0;
	height: 1px;
	width: 0;
	border-bottom: 1px solid #b357e8;
	transition: width 0.2s ease-in;
}

.headerLink:hover:before {
	width: 100%;
	border-bottom: 1px solid #b357e8;
}

#contact {
	cursor: pointer;
	margin: 0 1.2vw;
}

.activeTab {
	border-bottom: 1px solid #b357e8;
}

#github {
	cursor: pointer;
	width: 40px;
}
a {
	align-self: center;
	margin-left: 6vw;
	margin-right: 10.5vw;
	padding-top: 2px;
	height: 3vw;
	width: 40px;
}

body {
	font-family: "Peddana", serif;
	padding: 0;
	margin: 0;
}

.pageTitle {
	text-align: center;
	font-size: 3vw;
	margin: 1vw 0;
}

.inputForm {
	margin: 1.5vw auto;
	width: 45vw;
}

.inputLabel {
	font-size: 2vw;
}

.inputField {
	width: 95%;
	padding: 4px;
	margin-bottom: 8px;
	font-size: 1vw;
	border-radius: 5px;
	border: 1px solid #9b9b9b;
}

.inputFieldHidden {
	position: relative;
	margin: 1.5vw auto;
	width: 51vw;
	padding: 2px 5px;
	font-size: 2.3vw;
	text-align: center;
}

.inputFieldHidden:hover {
	cursor: pointer;
}

.inputFieldHidden:hover #editIcon {
	opacity: 1;
	transform: scale(1.08);
}

.inputFieldHidden:before {
	content: "";
	position: absolute;
	left: 25%;
	right: 25%;
	bottom: 0;
	height: 1px;
	width: 50%;
	border-bottom: 1px solid #b6b6b6;
	transition: opacity 0.5s;
}

.inputFieldHidden:hover:before {
	opacity: 0;
}

.inputFieldHidden:after {
	content: "";
	position: absolute;
	width: 0px;
	margin-left: auto;
	margin-right: auto;
	left: 0;
	right: 0;
	bottom: 0;
	height: 1px;
	border-top: 0px solid #b6b6b6;
	transition: width 0.4s ease-in;
}

.inputFieldHidden:hover:after {
	border-top: 1px solid #b357e8;
	width: 60%;
}

#editIcon {
	position: absolute;
	right: 4px;
	bottom: 4px;
	width: 1.7vw;
	height: 1.7vw;
	opacity: 0.5;
	transition: all 0.6s;
}

.submitBtn {
	padding: 0.7vw 4vw;
	text-align: center;
	cursor: pointer;
	position: relative;
	background: none;
	margin-top: 5px;
	border: 1px solid #9b9b9b;
	border-radius: 4px;
	overflow: hidden;
}

.submitBtn:hover {
	color: black;
}

.submitBtn:before {
	content: "";
	position: absolute;
	transition: all 0.2s ease-in;
	width: 100%;
	height: 400%;
	top: 0;
	left: 0;
	background: #b357e8;
	opacity: 0.4;
	transform: translateX(-100%) rotate(45deg);
}

.submitBtn:hover:before {
	transform: translateX(-26.5%) rotate(45deg);
}

.submitBtn:active:before {
	opacity: 0.9;
}

.submitBtn:after {
	content: "";
	position: absolute;
	transition: all 0.3s;
	width: 100%;
	height: 400%;
	top: 0;
	left: 0;
	background: #b357e8;
	opacity: 0.4;
	transform: translateY(-80%) translateX(100%) rotate(-45deg);
}
.submitBtn:hover:after {
	transform: translateY(-80%) translateX(35%) rotate(-45deg);
}

.submitBtn:active:after {
	opacity: 0.8;
}

.containerCv {
	padding: 15px;
	margin: 1.5vw auto;
	width: 55vw;
	min-height: 40vh;
	border-radius: 5px;
	background-color: rgb(235, 235, 235);
	/* effect of stacked sheets of paper */
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15), 0 10px 0 -5px #eee,
		0 10px 1px -4px rgba(0, 0, 0, 0.15), 0 20px 0 -10px #eee,
		0 20px 1px -9px rgba(0, 0, 0, 0.15);
}

.placeHolderCv {
	font-size: 2.3vw;
	text-align: center;
	line-height: 40vh;
}

#name {
	font-size: 2.7vw;
	margin-bottom: -0.4vw;
	text-align: center;
}

.sectionTitle {
	position: relative;
	font-size: 2.7vw;
	padding-top: 10px;
	margin-bottom: -13px;
	margin-top: 13px;
}

.sectionTitle:before {
	content: "";
	position: absolute;
	left: 25%;
	right: 25%;
	top: 0;
	width: 47%;
	margin: 0 auto;
	border-top: 1px solid black;
}

.sectionInfo {
	font-size: 1.9vw;
	margin-left: 20px;
	margin-bottom: 4px;
}

.expName {
	font-size: 2.1vw;
	font-weight: bold;
	margin-top: 1.3vw;
	margin-left: 7px;
}

.date {
	font-size: 1.4vw;
}

.expDivCV {
	position: relative;
}
.expDivCV:hover #editIconCV {
	opacity: 1;
}
.expDivCV:hover #removeIconCV {
	opacity: 1;
}

#editIconCV {
	position: absolute;
	right: 40px;
	top: 0;
	cursor: pointer;
	width: 1.6vw;
	height: 1.6vw;
	opacity: 0.1;
	transition: all 0.3s ease-out;
}

#editIconCV:hover {
	transform: scale(1.2);
}

#removeIconCV {
	position: absolute;
	right: 0;
	top: 0;
	cursor: pointer;
	width: 1.6vw;
	height: 1.6vw;
	opacity: 0.1;
	transition: all 0.5s ease-out;
}

#removeIconCV:hover {
	transform: scale(1.2);
}

.footer {
	margin-bottom: 0px;
	margin-top: 5vw;
	padding: 3px;
	background-color: #d9f7c7;
}

.footerContent {
	margin: 0 auto;
	text-align: center;
	font-size: 1.7vw;
	width: 40vw;
	line-height: 2vw;
}

